body {
  margin: 0px;
}

h1 {
  margin: 0px;
}

h3 {
  margin: 0px;
}

p {
  margin: 0px;
}

.container {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
}

.time-left-from-now-title {
    margin-top: 100px;
}

.time-left-from-now {
  font-size: 72px;
  margin-top: 10px;
}

.wrapper {
}

.wrapper h1 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 72px;
  color: black;
  text-align: center;
  position: absolute;
  left: 50%;
  /* I think this works for centering? */
  top: calc(50% - 64px);
  -webkit-transform: translate(-50%, calc(-50% - 64px));
  -ms-transform: translate(-50%, calc(-50% - 64px));
  transform: translate(-50%, calc(-50% - 64px));
}

.wrapper h1 span {
  position: absolute;
}